import React from 'react';
import PropTypes from 'prop-types'

import vkPic from './images/vk.svg'
import searchPic from './images/search.svg'
import youtubePic from './images/youtube.svg'
import facebookPic from './images/facebook.svg'
import instagramPic from './images/instagram.svg'

//import { ReactComponent as MenuButton } from './images/menu-button.svg'
export default class Header extends React.Component{
    static propTypes = {
        data: PropTypes.object
    }
    static defaultProps = {
        data: {}
    }
    constructor(props) {
        super(props);

        this.state = {
            mobileMenuIsShow: false,
            mobileContactsIsShow: false,
            mobileSearchRef: React.createRef(),
        };
    }
    handleMenuToggle = (state, menu) => {
        if (menu) {
            this.setState( { [menu]: state === undefined ? state : !this.state[menu] })
        } else {
            this.setState({ mobileMenuIsShow: false, mobileContactsIsShow: false })
        }
    };
    render(){
        // console.log(this.props.data && (this.props.data.menu));
        return (
            <React.Fragment>
            <div className="header pos-fixed w-full" id="anchorTop">
                <div className="social-block dis-flex">
                    <button className="social-block__search">
                        <img className="social-block__search-img" width="18" height="18" src={searchPic} alt=""/>
                    </button>
                    <div className="social-block__elems dis-flex m-l-5">
                        <a href={this.props.data.social && this.props.data.social.vk} className="social-block__elem">
                            <img className="social-block__elem-img" width="20" height="20" src={vkPic} alt=""/>
                        </a>
                        <a href={this.props.data.social && this.props.data.social.facebook} className="social-block__elem m-l-5">
                            <img className="social-block__elem-img" width="20" height="20" src={facebookPic} alt=""/>
                        </a>
                        <a href={this.props.data.social && this.props.data.social.instagram} className="social-block__elem m-l-5">
                            <img className="social-block__elem-img" width="20" height="20" src={instagramPic} alt=""/>
                        </a>
                        <a href={this.props.data.social && this.props.data.social.youtube} className="social-block__elem m-l-5">
                            <img className="social-block__elem-img" width="20" height="20" src={youtubePic} alt=""/>
                        </a>
                    </div>
                </div>
                <div className="m-t-5 header-menu">
                    <nav className="menu">
                        <ul className="menu__list">
                            {this.props.data.menu && this.props.data.menu.map(item => {
                                return <li key ={item.id} className="menu__group"><a href={item.path} className="menu__link">{item.title}</a></li>;
                            })}
                        </ul>
                    </nav>
                </div>
            </div>
            
            </React.Fragment>
        );
    }
}