import React from 'react';

import vkImage from "./images/vk.svg";
import instagramImage from "./images/instagram.svg";
import facebookImage from "./images/facebook.svg";
import youtubeImage from "./images/youtube.svg";

export default class CommonSocial extends React.Component{
    
    render(){
        return (
            <React.Fragment>
                <a href={this.props.vk} target="_blank" rel="noopener noreferrer">
                    <img src={vkImage} alt=""/>
                </a>
                <a href={this.props.instagram} target="_blank" rel="noopener noreferrer">
                    <img src={instagramImage} alt=""/>
                </a>
                <a href={this.props.facebook} target="_blank" rel="noopener noreferrer">
                    <img src={facebookImage} alt=""/>
                </a>
                <a href={this.props.youtube} target="_blank" rel="noopener noreferrer">
                    <img src={youtubeImage} alt=""/>
                </a>
            </React.Fragment>
        );
    }
}