import React from 'react';
import { NavLink } from "react-router-dom";

import addressItem from '../../../assets/images/location.png'
import './EventItem.css'

export default class EventItem extends React.Component{
    render(){
        console.log(this.props.event)
        return(
                <NavLink to={`/events/${this.props.event.id}`} className="dis-flex pos-relative events-block_elem-href">
                    <div className="events-block_elem-image">
                        <img src={process.env.REACT_APP_API_BASE_URL + this.props.event.image} alt="" className="sizefull"/>
                    </div>
                    <div className="events-block_elem-title dis-flex main-page-title"><span className="m-full-auto">{this.props.event.title}</span></div>
                    {
                        this.props.event.date && (
                            <div className="events-block_elem-date">
                                <p className="p-l-5 p-r-5">{this.props.event.date}</p>
                            </div>
                        )
                    }
                    {
                        this.props.event.address && (
                            <div className="events-block_elem-address dis-flex">
                                <div className="events-block_elem-icon">
                                    <img src={addressItem} alt="" className="p-t-5"/>
                                </div>
                                <p className="p-l-5 p-t-5 p-r-5">{this.props.event.address}</p>
                            </div>
                        )
                    }
                </NavLink>
        )
    }
}