import axios from 'axios'

export default {
    getMainNews() {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/common/news/`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    },
    getAllNews(params = '') {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/news/list/${params}`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    }
}