import React from 'react';

import './FooterContacts.css'

import phone from './images/phone.svg';
import mail from './images/mail.svg';
import location from './images/location.svg';

export default class FooterContacts extends React.Component{
    
    render(){
         console.log(this.props && (this.props));
        return (
            <div className="footer-contacts_block">
                <a href={"tel:" + this.props.phone} className="footer-contacts_item">
                    <span className="footer-contacts_icon">
                        <img src={phone} alt=""/>
                    </span>
                    <span className="footer-contacts_text">{this.props.phone}</span>
                </a>
                <a href={"mailto:" + this.props.email} className="footer-contacts_item">
                    <span className="footer-contacts_icon">
                        <img src={mail} alt=""/>
                    </span>
                    <span className="footer-contacts_text">{this.props.email}</span>
                </a>
                <a href={this.props.map} className="footer-contacts_item" target='_blank' rel="noopener noreferrer">
                    <span className="footer-contacts_icon">
                        <img src={location} alt=""/>
                    </span>
                    <span className="footer-contacts_text">{this.props.address}</span>
                </a>
            </div>
        );
    }
}