import React from 'react';
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';

import FooterContacts from '../../Shared/FooterContacts/FooterContacts';
import CommonSocial from '../../Shared/CommonSocial/CommonSocial'
export default class Footer extends React.Component{
    static propTypes = {
        data: PropTypes.object
    }
    static defaultProps = {
        data: {}
    }
    
    
    render(){
        // console.log(this.props.data && (this.props.data.menu));
        return (
            <footer id="footer" className="footerBlock">
                <div className="container">
                    <div className="row">
                        <div className="col-xl-6 col-4 d-none d-sm-block">
                            <div className="row">
                                <div className="col-xl-6">
                                    <div className="footer-contacts_title">Контакты</div>
                                    <FooterContacts {...this.props.data.contacts} />
                                </div>
                                <div className="col-xl-6">
                                    <div className="footer-contacts_title">Для СМИ</div>
                                    <FooterContacts {...this.props.data.massMedia} />
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-3 col-4 d-none d-sm-block">
                            <div className="footer-contacts_title">Навигация</div>
                            <nav className="linkList">
                                {this.props.data.menu && this.props.data.menu.map(item => (
                                    <Link to={item.path} key={item.id}>{item.title}</Link>
                                ))}
                            </nav>    
                        </div>
                        <div className="col-xl-3 col-4 d-none d-sm-block">
                            <div className="footer-contacts_title">Партнеры</div>
                            
                            <div className="footer-partners">
                                {this.props.data.partners && this.props.data.partners.map(item => (
                                    <a href={item.url} key={item.id}>
                                        <img src={process.env.REACT_APP_API_BASE_URL + item.image} alt=""/>
                                    </a>
                                ))}
                            </div>

                            <div className="footer-contacts_title">Соцсети</div>

                            <div className="footer-social">
                                <CommonSocial {...this.props.data.social} />
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="copyright">
                                {this.props.data.main && this.props.data.main.copyright}
                            </div>
                        </div>
                    </div>
                </div>
            </footer>
        );
    }
}