import React from 'react';
import { NavLink } from 'react-router-dom'
import { ScreenWidth } from '../../../../../context'
import Slider from 'react-slick';

import newsService from "../../../../../services/news.service"
import "./NewsBlock.css"

import NewsItem from "../../../../Shared/NewsItem/NewsItem";

export default class NewsBlock extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            news: [],
            carouselSettings: {
                dots: true,
                arrows: false,
                infinite: false,
                slidesToShow: 2,
                slidesToScroll: 1,
                responsive: [
                    {
                        breakpoint: 550,
                        settings: {
                            slidesToShow: 1,
                        }
                    }
                ]
            }
        }
    }
    componentDidMount() {
        newsService.getMainNews()
            .then(response => {
                //console.log(response)
                this.setState({ news: response.data })
                
            })
            .catch(error =>{
                alert(error)
            })
    }

    newsRender(screenWidth){
        if (screenWidth > 767){
            console.log(this.state && this.state.news);
            return(
                <div className="news-block_all dis-flex">
                    {this.state.news.map(item => (
                        <div key={item.id} className="news-block_elem">
                            <NewsItem news={item} key={item.id} />
                        </div>
                    ))}
                </div>
            )
        }
        else {
            return(
                <Slider className='slider-main slider-dotted-line' {...this.state.carouselSettings}>
                    {this.state.news.map(item => (
                        <NewsItem news={item} key={item.id} />
                    ))}
                </Slider>
            )
        }
    }

    render(){
        return(
            <ScreenWidth.Consumer>
                {({screenWidth}) => (
                    <section className="news-block main-page-grey-section" id="anchorTop">
                        <div className = "container">
                            <div className="dis-flex p-t-20">
                                <div className="title_block">
                                    <h1>Новости</h1>
                                </div>
                                <div className="allnews_button main-page-button d-lg-block d-none">
                                    <NavLink className="main-page-button" to="/news">
                                        все новости
                                    </NavLink>
                                </div>
                            </div>
                            {this.newsRender(screenWidth)}
                        </div>
                    </section>
                )}
            </ScreenWidth.Consumer>
        )
    }
}