import React from 'react';
import { NavLink } from "react-router-dom";
import './NewsItem.css'

export default class NewsItem extends React.Component{
    render(){
        return(
                <NavLink to={`/news/${this.props.news.id}`} className="dis-flex pos-relative news-block_elem-href">
                    <div className="news-block_elem-image">
                        <img src={process.env.REACT_APP_API_BASE_URL + this.props.news.image} alt="" className="sizefull"/>
                    </div>
                    <div className="news-block_elem-title main-page-title dis-flex"><span className="m-full-auto">{this.props.news.title}</span></div>
                </NavLink>
        )
    }
}