import React from 'react';
import { NavLink } from "react-router-dom";
import helper from '../../../../helpers'
import newsMore from "../../../../assets/images/news-more.svg";
export default class NewsListItem extends React.Component{
    previewText(string){
        return string && (string < 300 ? string : string.slice(0, 300) + '...');
    }
    
    render() {
        //console.log(this.props && (this.props));
        return (
            <NavLink to={`/news/${this.props.id}`} className="news-list__item">
                <div className="news-list__image">
                    <img src={process.env.REACT_APP_API_BASE_URL + this.props.image} alt=""/>
                </div>
                <div className="news-list__content">
                    <div className="news-list__date">{helper.convert2LL(this.props.date)}</div>
                    <div className="news-list__title">{this.props.title}</div>
                    <div className="news-list__preview-text">
                        <p>{this.previewText(this.props.titlePreview)}</p>
                    </div>


                    <div className="text-right">
                        <div className="news-list__more">
                            <span>подробнее</span>
                            <img src={newsMore} alt=""/>
                        </div>
                    </div>
                </div>
            </NavLink>
        );
    }
}