import axios from 'axios'

export default {
    getAllMedia() {
        return axios.get(`${process.env.REACT_APP_API_BASE_URL}/api/common/media/`, {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': "*"
            }
        })
    }
}