import React from 'react';
import Slider from 'react-slick';
import { ScreenWidth } from '../../../../../context'

import "./MediaBlock.css"

import MagnificPopup from '../../../../Shared/MagnificPopup/MagnificPopup';
import helper from '../../../../../helpers';
import mediaService from '../../../../../services/media.service';

export default class MediaBlock extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            photos: [],
            videos: [],
            urlvideos: "",
            urlphotos: "",
            videoSettings: {
                dots: false,
                autoplay: true,
                autoplaySpeed: 5000,
                arrows: false,
                infinite: true,
                slidesToShow: 1,
                slidesToScroll: 1,
                responsive: [
                    {
                        breakpoint: 650,
                        settings: {
                            arrows: false,
                            autoplay: false
                        }
                    }
                ]
            },
            popup: {
                isShow: false,
                videoId: null,
                videoTitle: ''
            },
            photoSettings: {
                dots: false,
                arrows: false,
                infinite: false,
                slidesToShow: 3,
                slidesToScroll: 1,
                responsive: [
                    {
                        breakpoint: 1010,
                        settings: {
                            slidesToShow: 2
                        }
                    },
                    {
                        breakpoint: 550,
                        settings: {
                            slidesToShow: 1
                        }
                    }
                ]
            }
            
        }
    }

    componentDidMount() {
        mediaService.getAllMedia()
            .then(response => {
                console.log(response)
                this.setState({ 
                     photos: response.data.photos,
                     videos: response.data.videos,
                     urlvideos: response.data.urlvideos,
                     urlphotos: response.data.urlphotos
                })
                
            })
            .catch(error =>{
                alert(error)
            })
    }
    openVideoPopup = (videoId, videoTitle) => {
        this.setState({ popup: { isShow: true, videoId: videoId, videoTitle: videoTitle } });
    };

    closePopup = () => {
        this.setState({ popup: { isShow: false, videoId: null, videoTitle: '' } });
    };
    

    render() {
        return (
            <ScreenWidth.Consumer>
                {({screenWidth}) => (
                    <section id="mediafiles" className="media-block m-t-80 main-page-grey-section">
                        <div className="container">
                            <div className="title_block m-l-35">
                                <h1>Медиа</h1>
                            </div>
                            <Slider {...this.state.photoSettings} className="media-mobile-carousel d-lg-none">
                                {this.state.photos.map(item => (
                                    <a href={item.url} key={item.id} target="_blank" rel="noopener noreferrer" className="photo-item">
                                        <img src={process.env.REACT_APP_API_BASE_URL + item.image} alt=""/>
                                        <span className="photo-item__title dis-flex m-r-0 m-l-0 m-b-0 p-t-10 p-r-25 p-b-0 p-l-25">{item.title}</span>
                                    </a>
                                ))}

                            </Slider>
                            <div className="row">
                                <div className="col-xl-6 d-none d-lg-block">
                                    <div className="row">
                                        {this.state.photos.map(item => (
                                            <div className="col-6" key={item.id}>
                                                <a href={item.url} target="_blank" rel="noopener noreferrer" className="photo-item">
                                                    <img src={process.env.REACT_APP_API_BASE_URL + item.image} alt=""/>
                                                    <span className="photo-item__title dis-flex m-r-0 m-l-0 m-b-0 p-t-10 p-r-25 p-b-0 p-l-25">{item.title}</span>
                                                </a>
                                            </div>
                                        ))}
                                    </div>
                                    <div className="text-right pt-4 photos-block_button">
                                        <a href={this.state.urlphotos} target="_blank" rel="noopener noreferrer" className="media-button main-page-button">все фото</a>
                                    </div>
                                </div>
                                <div className="col-xl-6">
                                    <Slider className="video-block" {...this.state.videoSettings}>
                                        {this.state.videos.map(item => (
                                            <div className="video-item" key={item.id} onClick={() => this.openVideoPopup(helper.getVideoId(item.url), item.title)}>
                                                <div className="video-item__image">
                                                    <img src={`https://img.youtube.com/vi/${helper.getVideoId(item.url)}/hqdefault.jpg`} alt=""/>
                                                </div>
                                                <div className="video-item__title">{item.title}</div>
                                            </div>
                                        ))}
                                    </Slider>
                                    <div className="text-right pt-4 d-none d-sm-block video-block_button m-t-15">
                                        <a href={this.state.urlvideos} target="_blank" rel="noopener noreferrer" className="video-button main-page-button">все видео</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {this.state.popup.isShow &&
                        <MagnificPopup
                            videoId={this.state.popup.videoId}
                            videoTitle={this.state.popup.title}
                            onClose={this.closePopup}
                        />}
                    </section>
                )}
            </ScreenWidth.Consumer>
        );
    }
}