import React from 'react';
import { NavLink } from 'react-router-dom'
import { ScreenWidth } from '../../../../../context'
import Slider from 'react-slick';

import eventsService from "../../../../../services/events.service"
import "./EventBlock.css"

import EventItem from "../../../../Shared/EventItem/EventItem";

export default class EventBlock extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            events: [],
            carouselSettings: {
                dots: true,
                arrows: false,
                infinite: false,
                slidesToShow: 2,
                slidesToScroll: 1,
                responsive: [
                    {
                        breakpoint: 550,
                        settings: {
                            slidesToShow: 1,
                        }
                    }
                ]
            }
        }
    }
    componentDidMount() {
        eventsService.getMainEvents()
            .then(response => {
                //console.log(response)
                this.setState({ events: response.data })
                
            })
            .catch(error =>{
                alert(error)
            })
    }

    eventsRender(screenWidth){
        if (screenWidth > 767){
            console.log(this.state && this.state.events);
            return(
                <div className="events-block_all dis-flex">
                    {this.state.events.map(item => (
                        <div key={item.id} className="events-block_elem">
                            <EventItem event={item} key={item.id} />
                        </div>
                    ))}
                </div>
            )
        }
        else {
            return(
                <Slider className='slider-main slider-dotted-line' {...this.state.carouselSettings}>
                    {this.state.events.map(item => (
                        <EventItem event={item} key={item.id} />
                    ))}
                </Slider>
            )
        }
    }

    
    render(){
        return(
            <ScreenWidth.Consumer>
                {({screenWidth}) => (
                    <section className="events-block">
                        <div className = "container">
                            <div className="dis-flex events_block-title">
                                <div className="title_block">
                                    <h1>События</h1>
                                </div>
                                <div className="allevents_button main-page-button">
                                    <NavLink className='eventsblock-nav_active' to="/opportunity/active">
                                        <span>актуальные</span>
                                    </NavLink>
                                    <NavLink className='eventsblock-nav' to="/opportunity/completed">
                                        <span>прошедшие</span>
                                    </NavLink>
                                </div>
                            </div>
                            {this.eventsRender(screenWidth)}
                        </div>
                    </section>
                )}
            </ScreenWidth.Consumer>
        )
    }
}